<script>
export default {
  data() {
    return {
      inputNewStudent: '',
    };
  },

  computed: {
    addNewStudent() {
      return {
        firstname: this.inputNewStudent,
      };
    },
  },

  // props: ['student'],
  methods: {
    addStudent() {
      this.$emit('addStudent', this.addNewStudent);
      this.inputNewStudent = '';
    },
  },
};
</script>

<template>
<div>
  <h1>Add a Student</h1>
  <input v-model='inputNewStudent'>
  <button v-on:click='addStudent()'>Add a Student</button>
</div>
</template>

