<script>
import axios from 'axios';
import StudentLine from './StudentLine';
import AddStudent from './AddStudent';

const BACK_URL = 'http://localhost:8000/api'

export default {
  components: {
    StudentLine,
    AddStudent,
  },
  data() {
    return {
      students: [],
    };
  },
  methods: {
    getAll() {
      axios.get(`${BACK_URL}/students/`)
        .then((response) => {
          this.students = response.data;
        });
    },
    remove(index) {
      axios.delete(`${BACK_URL}/students/${this.students[index].id}`)
        .then(() => {
          this.students.splice(index, 1);
          console.log(index);
        });
    },
    addStudentAAAA(objectStudent) {
      axios.post(`${BACK_URL}/students/`, objectStudent)
        .then((response) => {
          this.students.push(response.data);
          console.log(response);
        });
    },
    editStudentAAA(editValue, index) {
      axios.put(`${BACK_URL}/students/${this.students[index].id}/`, editValue)
        .then((response) => {
          console.log(response.data);
          console.log(this.students[index]);
          this.$set(this.students, index, response.data);
          //this.students.splice(index, 1, editValue);
          // this.students[index].firstname = response.data.firstname;
        });
    },
  },
  mounted() {
    this.getAll();
  },
};
</script>

<template>
<div>
  <add-student v-on:addStudent="addStudentAAAA"></add-student>
  <student-line v-for='(student, index) in students' :key="student.id" :indexA="index" :student="student" v-on:remove="remove(index)" v-on:edit='editStudentAAA'></student-line>
</div>
</template>

<style>
</style>



